	module.exports = {"data":{"post":{"html":"<p>At work I've been looking pretty hard the past couple of days at <a href=\"http://www.nagios.org/\">Nagios</a>, a web-based network monitoring system formerly konwn as <a href=\"http://www.netsaint.org/\">NetSaint</a>.  We currently have a combination of <a href=\"http://pikt.org\">pikt</a> and a home-brewed SMS notification/ticketing system that's powerful but difficult to update and expand. We'd like to be able to add new systems to the montioriing with a couple of web clicks (instead of editing a bunch of text files). Nagios might be able to do that, and if I can somehow connect the SMS system to Nagios, we'll be better off. (For Americans, SMS is text messaging via cell phone.)</p>","fields":{"title":"Nagios","date":"05 Jul 2002","url":"https://papascott.de/2002-07-05-nagios/"}}},"pathContext":{"slug":"/2002-07-05-nagios/","prev":{"fields":{"title":"Pardon More of Our Dust","date":"04 Jul 2002","slug":"/2002-07-04-pardon-more-of-our-dust/"}},"next":{"fields":{"title":"TrackBack For The Rest Of The World","date":"06 Jul 2002","slug":"/2002-07-06-trackback-for-the-rest-of-the-world/"}}}}

